.checkout {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: white;
    height: max-content;
}

.checkoutTitle {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

.checkoutLeft {
    display: flex;
    flex-direction: column;
}